<template>
	<el-row class="wrapper" id="wrapperbind">
		<el-form label-width="120px"  ref="ruleForm" :model="ruleForm" :rules="nodeRules">
			<el-form-item label="节点编号：" prop="file" id="wrapperForm">
				<el-input type="textarea" v-model="ruleForm.file" rows="12" ></el-input>
				<span>支持换行添加多个节点</span>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="submitForm('ruleForm')">确定</el-button>
				<el-button @click="cancleForm('ruleForm')">取消</el-button>
			</el-form-item>
		</el-form>
	</el-row>
</template>
<style lang="less">
@import '../../../theme/theme.less'; 
#wrapperbind{
  text-align:right;
}
#wrapperForm .el-form-item__error {
    color: #ff4949;
    font-size: 12px;
    line-height: 1;
    padding-top: 4px;
    position: absolute;
    top: 100%;
    right: 0;

}

</style>
<script>
module.exports = require('./bindNode');

</script>



